import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'

Vue.use(Router)
const router = new Router({
  routes,
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    from.meta.scrollTop = document.querySelector('.main-container')
      ? document.querySelector('.main-container').scrollTop
      : 0
  }
})
router.beforeEach((to, from, next) => {
  if (to.meta.isAuth) { // 
    if (JSON.parse(localStorage.getItem('isLogin'))) { // access_token
      next()
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    next()
  }
  /*   token    */
  if (to.fullPath === '/login') {
    if (JSON.parse(localStorage.getItem('isLogin'))) {
      next({
        path: from.fullPath
      })
    } else {
      next()
    }
  }
})
router.afterEach((to, from) => {
  if (router.app) {
    setTimeout(() => {
      if (document.querySelector('.main-container') && to.meta.scrollTop) {
        document.querySelector('.main-container').scrollTop = 0
        // to.meta.scrollTop || 0
      }
    }, 1000)
  }
})

export default router
